import React from "react"
import styled from "styled-components"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"

import Layout from "../../components/layout"
import SEO from "../../components/seo"
import { theme } from "../../theme/GlobalStyles"
import ProjectNav from "../../components/projectnav"
import ProjectText from "../../components/projecttext"

const Img1 = styled(Img)`
    width: 45%;
    display: inline-block;
    margin: 1%;
    @media (max-width: ${theme.mobile}) {
        width: 80vw;
    }
`
const Img2 = styled(Img)`
    width: 45%;  
    display: inline-block;
    margin: 1%;
    @media (max-width: ${theme.mobile}) {
        width: 80vw;
    }  
`
const EdenPage = () => {
    const data = useStaticQuery(graphql`
        query {
            img1: file(relativePath: { eq: "eden/eden-home.png" }) {
                ...image
            }
            img2: file(relativePath: { eq: "eden/eden-news.png" }) {
                ...image
            }
        }
    `)

  return (
    <Layout>
      <SEO title="Eden Chen Music" />
      <ProjectNav active="Eden Chen Music">
          <ProjectText
            type="WEB DEV & UI/UX DESIGN"
            title="EDEN CHEN MUSIC"
            tools="REACTJS, GATSBY"
            body1="Had fun designing and coding a personal website for my brother! The goal was to create a sleek and minimal design that could showcase high resolution pictures, but also present information in a straight-forward manner."
            body2="The website was originally coded a year ago when I was just starting to learn web dev. It had a poor performance time, since it was coded in plain HTML/JavaScript/CSS with Bootstrap, with minimal optimization. This summer, I redid the whole thing from scratch on ReactJS and Gatsby, so that I could optimize image laoding, and make reusable components for easier maintainability."
            link="Visit Website"
            url="https://edenchenmusic.com/">
          </ProjectText>
          <Img1 fluid={data.img1.childImageSharp.fluid} />
          <Img2 fluid={data.img2.childImageSharp.fluid} />
      </ProjectNav>
    </Layout>
  )
}
export default EdenPage